<template>
    <nacl-page-container no-header>
        <nacl-page-content id="type">
            <!-- <h1>商品分类</h1> -->
            <nacl-flexbox
                justify="between"
                class="list"
            >
                <ul class="left">
                    <li
                        v-for="item in CommodityType"
                        :key="item.category"
                        @click="categorySwitch(item.category)"
                    >
                        <p :class="{'active': item.category == category}">{{ item.title }}</p>
                    </li>
                </ul>
                <nacl-flexbox class="right">
                    <div
                        v-for="item in activeData"
                        :key="item.type"
                        class="item"
                    >
                        <a  @click="goCommodityList('/'+item.title)">
                            <img :src="item.imgSrc">
                            <p class="title">{{ item.title }}</p>
                        </a>
                    </div>
                </nacl-flexbox>
            </nacl-flexbox>
            <p class="recommended">- 时尚专区 -</p>
            <recommendedList />
        </nacl-page-content>
        <basic-footer />
    </nacl-page-container>
</template>

<script>
import axios from 'axios'
import recommendedList from '../components/recommended-list'

export default {
    name: 'type',
    components: {
        recommendedList
    },
    data () {
        return {
            CommodityType: [],
            category: null
        }
    },
    computed: {
        activeData () {
            if (!this.category) return []
            let index = this.CommodityType.findIndex((item) => item.category === this.category)
            return this.CommodityType[index].children
        }
    },
    created () {
        this.getType()
    },
    methods: {
        goRouting(param){
            this.toGetUser();
            window.webkit.messageHandlers.goRouting.postMessage(param);
        },
        toGetUser(){
            const cutCount = Math.ceil(JSON.stringify(localStorage).length / 1000);
            for(let i = 0 ; i<cutCount;i++){
                if(i!=cutCount-1){
                    if(i==0){
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }else{
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }
                    
                }else{
                    window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,JSON.stringify(localStorage).length)));
                }
            }
        },
        goCommodityList(param){
            this.toGetUser();
            window.webkit.messageHandlers.commodityList.postMessage(param);
        },
        categorySwitch (category) {
            this.category = category
        },

        getType () {
            axios.get('http://47.106.209.243:5000/api/commodity/getType').then((rq) => {
                this.CommodityType = rq.data.data
                this.categorySwitch(this.CommodityType[0].category)
                // console.log(this.CommodityType)
            })
        }
    },
    mounted(){
        window.goRouting = this.goRouting;
    }
}
</script>
<style lang="scss" scoped>
.recommended {
    padding-top: 30px;
    font-size: 32px;
    text-align: center;
    color: #4d83f7;
}
#type {
//   background: #fff;
}
h1 {
  padding: 32px 0;
  font-size: 44px;
  color: #212128;
  text-align: center;
  background: #fff;
}
.list {
    height: 700px;
  border-top: 2px solid #f2f2f2;
  background: #fff;
}
.left {
  width: 176px;
  font-size: 34px;
  color: #212128;
  text-align: center;
  p {
    height: 116px;
    line-height: 116px;
    background-color: #f9f9f9;
  }
  .active {
    background: #fff;
  }
}
.right {
  flex: 1 0;
  padding-top: 50px;
  text-align: center;
  flex-wrap: wrap;
  align-content: flex-start;
  img {
    width: 120px;
    height: 120px;
  }
  .item {
    width: 33%;
    padding-bottom: 56px;
    a {
      display: block;
    }
  }
}
</style>
